import React,{useState} from "react";
import './App.css';
import axios from 'axios';


function App() {

const [quote1,setQuote]= useState("");

//Get quote singular
  const getQuote = () =>{
    axios.get("https://api.kanye.rest").then(
      (response)=>{
        console.log(response);
        setQuote(response.data.quote)
    }
  );
}

const [quote2,setQuote2]=useState("");

//get two quotes converged
const getQuote2 = ()=>{
  axios.get("https://api.kanye.rest").then(
    (response2)=>{
      setQuote2(response2.data.quote)
    }
  
  )
}




return(
 <div className="app">

   <div className="card">
   <h1 className="title">Kanye West Quote Generator</h1>


     <h2 className="heading">"{quote1} ... {quote2}"</h2>

  <button className="button1" onClick={getQuote}>Get Quote</button>
  <button className="button1" onClick={getQuote2}>Conjoin Another</button>
</div>
</div>
)
}

export default App;
